@import "vars";

.contact-info {
  width: 362px;
  display: flex;
  flex-direction: column;

  .section__subheading {
    margin-bottom: 0;
  }

  &__action,
  &__link {
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 150%;
  }

  &__action {
    font-weight: 800;
    font-size: 13px;
    margin-top: 42px;
  }

  &__link {
    text-decoration: none;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    color: $mColor;

    &:focus {
      outline: $outline;
    }

    &:hover {
      color: $color2;
    }
  }

  &__sm-icons {
    display: flex;
    margin-top: 53px;
  }

  &__img {
    height: 32px;
    width: 32px;
  }

  &__sm-link {
    margin-right: 48px;

    &:focus {
      animation: swing 1s infinite ease;
      outline: none;
    }

    &:hover {
      animation: swing 1s ease;
      animation-iteration-count: 1;
    }
  }
}

@media (max-width: $mediumScreenBP) {
  .contact-info {
    width: 310px;

    &__sm-icons {
      align-self: center;
    }
  }
}

@media (max-width: 400px) {
  .contact-info {
    width: 100%;
  }
}
